import React from "react";
import { useState } from "react";
// import { NavLink } from "react-router-dom";

const Login = (props) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const login = () => {
    fetch("http://54.174.103.177/api/v1/users/login", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify({
        email: email,
        password: password,
      }),
    }).then((result) => {
      result.json().then((resp) => {
        console.log(resp.message, "message");
        if (resp.message == "User login succssful") {
          props.AllData(resp);
          props.LoginFunction(true);
        }
      });
    });
  };

  return (
    <>
      <div className="maincontainer">
        <div className="logincontainer">
          <div className="logo">
            <img
              src="https://smoothhiring.com/wp-content/uploads/2021/03/Smooth-hiring-logo-001-02.png"
              width="200"
              height="40"
              alt="logo"
            />
          </div>
          <div className="loginform">
            <div>
              <label for="email">Email </label>
              <div>
                <input
                  type="email"
                  name="email"
                  id="email"
                  value={email}
                  placeholder="Enter Your Email"
                  autoComplete="off"
                  required
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
            </div>
            <div>
              <label for="email">password </label>
              <div>
                <input
                  type="password"
                  name="password"
                  id="password"
                  value={password}
                  placeholder="Enter Your Password"
                  autoComplete="off"
                  required
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
            </div>
            <div className="checkbox_rmbr">
              <div className="rmbrme">
                <input type="checkbox" id="checkbox" />
                <span className="rmbr">
                  <label for="checkbox">Remember me</label>
                </span>
              </div>
              <div className="frgtpswd">
                <a href="#">Forgot password?</a>
                {/* <NavLink to="/FrgtPswrd">Forgot password?</NavLink> */}
              </div>
            </div>
          </div>
          <div className="LOGINBTN">
            <button className="loginbtn" onClick={login}>
              Login now
            </button>
          </div>
          <div className="accntcrt">
            <p>
              Dont have an Account?
              <span>
                <a href="">Sign Up</a>
              </span>
            </p>
          </div>
          <div className="or">
            <span>OR</span>
          </div>
          <div className="cnct_p">
            <p>Or Connect Us with</p>
          </div>
          <div className="social">
            <button className="googlebtn">
              <i class="fab fa-google"></i> Google
            </button>
            <button className="fbbtn">
              <i class="fab fa-facebook-square"></i> Facebook
            </button>
            <button className="lnkdnbtn">
              <i class="fab fa-linkedin"></i> Linkedin
            </button>
          </div>
          <div className="policy">
            <a href="#">Terms of use</a> | <a href="#">privacy Policy</a>
          </div>
          <div className="help">
            <a href="#">Need Help ?</a>
          </div>
        </div>
        <div className="welcomecontainer">
          <div className="ellipse1"></div>
          <div className="ellipse3"></div>

          <div className="about">
            <div className="Welcome_sh">
              <h1>
                Welcome to<span> SmoothHiring </span>
              </h1>
            </div>
            <div className="Welcome_p">
              <p>
                Don't have an account? Create your account, it takes less than a
                minute
              </p>
            </div>
            <div className="crtaccbtn">
              <button className="accountbtn">Create My Account</button>
            </div>
            <div className="Brdcst_dot">
              <div className="broadcast">
                <div>Broadcast to top job boards</div>
                <div>Assess Applicants</div>
                <div>Find the best fit</div>
                <div> Automate hiring</div>
              </div>
              <div className="ellipse2"></div>
              <div className="dotcontainer">
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
                <div>
                  <span className="dots"></span>
                  <span className="dots"></span>
                  <span className="dots"></span>
                </div>
              </div>
            </div>
            <div className="mail">
              <i class="fas fa-envelope"></i> help@
              <a href="https://www.smoothhiring.com">smoothhiring.com</a>
            </div>
            <div className="phone">
              <i class="fas fa-phone-alt"></i>1 (877) 789-8767
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
