import React from "react";
import CreateIcon from "@material-ui/icons/Create";
import KeyboardArrowRightIcon from "@material-ui/icons/KeyboardArrowRight";
const Main = (props) => {
  console.log(props, "props");
  let arr = [
    { nameofthejob: "Acupunturist" },
    { nameofthejob: "Paramedic" },
    { nameofthejob: "Buisness Manager" },
    { nameofthejob: "Account Manager" },
  ];
  return (
    <div className="Mainjob">
      <div className="logobox_Main">
        <div className="logo">
          <img
            src="https://smoothhiring.com/wp-content/uploads/2021/03/Smooth-hiring-logo-001-02.png"
            width="260"
            height="50"
            alt="logo"
          />
        </div>
        <div className="user_Main">
          <h3>
            {props.data.data.first_name} &nbsp;
            {props.data.data.middle_name}
            {props.data.data.last_name}
          </h3>
          <p>{props.data.data.email}</p>
        </div>
        <div className="alljobsdiv">
          <div className="alljobs">
            <b>All Jobs (19)</b>
          </div>
          <div className="Aj_Dj_Ej">
            <i class="fas fa-user-tie"></i> <b>Active Jobs (01)</b>
          </div>
          <div className="Aj_Dj_Ej">
            <i class="fas fa-pencil-alt"></i> <b>Draft Jobs (03)</b>
          </div>
          <div className="Aj_Dj_Ej">
            <i class="fas fa-ban"></i> <b>Expired Jobs (15)</b>
          </div>
        </div>
        <div className="leftbtns_Main">
          <div>
            <button className="Emailbtn_Main">Email Us</button>
            <div className="lftmail">
              <div>
                <i class="fas fa-envelope"></i> help@
              </div>
              <a href="https://www.smoothhiring.com">smoothhiring.com</a>
            </div>
          </div>
          <div>
            <button className="Supportbtn_Main">Contact Us</button>
            <div className="lftphone">
              <i class="fas fa-phone-alt"></i>1 (877) 789-8767
            </div>
          </div>
        </div>
      </div>

      <div className="NavContainer_Main">
        <nav className="navbar_Main">
          <div>
            <ul className="list">
              <li>
                <a href="#">
                  <i class="fas fa-dollar-sign"></i> Pricing
                </a>
              </li>
              <li>
                <a href="#">
                  <i class="fas fa-cog"></i> Settings
                </a>
              </li>
              <li>
                <a href="#">
                  <i class="far fa-question-circle"></i> Help
                </a>
              </li>
              <li>
                <a href="#">
                  <i class="fas fa-sign-out-alt"></i> Log out
                </a>
              </li>
            </ul>
          </div>
        </nav>
        <div className="whole">
          <div className="Main_btns">
            <div className="job_intrv_btns">
              <button className="jobbtn">My Jobs</button>
              <button className="intrbtn">My Interviews</button>
            </div>
            <div>
              <button className="crtbtn">Create New Job</button>
            </div>
          </div>
          <div className="containermain">
            <div className="sort">
              <p>Sort by: abc</p>
            </div>
            {arr.map((item, index) => {
              return (
                <div className="All">
                  <div className="jobnames">
                    {index + 1}. <CreateIcon className="createbtn" />
                    <b>{item.nameofthejob}</b>
                  </div>
                  <div className="rtARwBtn">
                    <KeyboardArrowRightIcon className="rightarrow" />
                    <button className="Starthiringbtn">Start Hiring</button>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Main;
